import { Component, OnInit } from '@angular/core';
import { ActivatedRouteSnapshot, ActivatedRoute } from '@angular/router';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { IonItem, NavController } from '@ionic/angular';
import { DialogService } from 'src/app/api/util/dialog.service';

import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { SocialShareComponent } from 'src/app/components/social-share/social-share.component';
import { UtilService } from 'src/app/services/util.service';
import { ListsHomeService } from 'src/app/services/lists-home.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { SharerService } from 'src/app/services/sharer.service';
import { ScreenLoadingService } from 'src/app/services/screen-loading.service';



@Component({
  selector: 'app-result',
  templateUrl: './result.page.html',
  styleUrls: ['./result.page.scss','../../home/results/results.page.scss'],
})
export class ResultPage implements OnInit {

  public id : any;
  public resultModel : Model;
  public game : any ;
  private firstLoading = true;

  constructor(
    public route : ActivatedRoute,
    public requesService : RequestService,
    private dialogService : DialogService,
    private socialSharing: SocialSharing,
    public util : UtilService,
    public listsHomeService : ListsHomeService,
    public navCtrl : NavController,
    public authUser : AuthUserService,
    public shareService : SharerService,
    public loading : ScreenLoadingService


  ) {
    this.id = this.route.snapshot.paramMap.get('id');
    this.resultModel = new Model('Game',requesService);

    //throw new Error('error test')

   }

  ngOnInit() {
    this.init();

    this.resultModel.api_functionModel(this.id,'setWinner').subscribe(
      response => {
        console.log('setWinner' , response);
      })
    
  }

  async init(event = null){
    
    if(this.firstLoading){
      this.loading.start()
      //await this.dialogService.loadingProgres();
      this.firstLoading = false;
    }
    
    this.resultModel.api_functionModel(this.id,'dataProfile').subscribe(data => {
      if(data['status'] == 'success')
        this.game = data['data'];
        if(event)
          event.target.complete();
        //this.dialogService.dimissLoaging();
        this.loading.end()
    },error => {
      //this.dialogService.dimissLoaging();
      this.loading.end()
    })
  }

  sendInstagram(){
    this.socialSharing.shareViaInstagram('Imagen','https://api.futbol-alem.com/imagecache/original/8lNFig1mR35YTYU.png');
  }


  async share(){
    await this.util.share();
  }

  confirmDelete(){
    this.dialogService.presentAlertConfirm('Confirmar','Estas seguro de eliminar este game',e => {
      this.delete()
    });
   }

   delete(){
     this.resultModel.api_delete(this.game.id).subscribe(data => {
       console.log(data);
       if(data['status'] == 'success'){
         this.dialogService.showToast('Partido Eliminado',null,'success');
         this.listsHomeService.remove('results',this.game.id);
         this.navCtrl.navigateRoot('/home/results');

         
       }
     });
   }


   get isAdminTeam(){
    return this.authUser.isAdminGame(this.game) || this.authUser.isAdmin();
   }

}
